import React from 'react';
import '../css/NavTop.css';
import {FavoriteBorder, AddShoppingCart,  NotificationsNone, Search} from '@material-ui/icons';
import NavSecond from './NavSecond';

const NavTop=()=> {
    return (<>
        <nav>
            <div className="header_logo">
                <img src="https://www.udemy.com/staticx/udemy/images/v6/logo-coral.svg" alt=""></img>
                <div className="dropdown">
                <p className="dropdown_title">
                    Categories
                </p>
                <div className="dropdown_content">
                    <ul>
                        <li>Development</li>
                        <li>Development</li>
                        <li>Business</li><li>Development</li>
                        <li>Business</li><li>Development</li>
                        <li>Business</li><li>Development</li>
                        <li>Business</li>
                        <li>Business</li>
                    </ul>
                </div>
                </div>
                
            </div>
            <div className="header_search">
                <Search className="search_header"/>
                <input type="text" placeholder="search for anything ..."></input>
            </div>
            <div className="header_item">
                <ul>
                    <li>Udemy for Business</li>
                    <li>Instructor</li>
                    <li>My Learning</li>
                </ul>
            <div className="header_icons">
                <FavoriteBorder className="icons_favourite" />
                <AddShoppingCart className="icons_cart"/>
                <NotificationsNone className="icons_bell"/>
                <span className="title">BS</span>
            </div>
            </div>
        </nav>
        <NavSecond /></>
    )
}

export default NavTop
