import React from 'react';
import {ArrowBackIos, ArrowForwardIos} from '@material-ui/icons';
import '../css/Learning.css';
const Learning = () =>{
    return (
        <div className="learning">
            <div className="learning_title">
                <h2>Let's Start learning, Binod</h2>
                <p>My Learning</p>
            </div>
            <div className="learning_carousel">
                <div className="video">
                    <img src="/public/images/istockphoto-951945718-612x612.jpg" alt="">
                    </img>
                    <img src="/public/images/product-img-1.jpg" alt=""></img>
                    <img src="/public/images/product-img-2.jpg" alt=""></img>
                </div>
                <div className="arrows_learning">
                    <ArrowBackIos />
                    <ArrowForwardIos />
                </div>
            </div>
        </div>
    )
}

export default Learning
